// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import Vuex from 'vuex'
import vax from './1.vax.xml'
import App from './App'
import Antd from 'ant-design-vue';
import 'ant-design-vue/dist/antd.css';
Vue.config.productionTip = false
Vue.use(Vuex)
vax.actions.LocalStream = ({ commit, state }, param) => {
  return new Promise(function (resolve, reject) {
    commit('LocalStream', param);
    resolve(param);
  });
}
vax.mutations.LocalStream = (state, data) => {
  state.LocalStream = data;
}
vax.actions.RemoteStreams = ({ commit, state }, param) => {
  return new Promise(function (resolve, reject) {
    commit('RemoteStreams', param);
    resolve(param);
  });
}
vax.mutations.RemoteStreams = (state, data) => {
  state.RemoteStreams = data;
}
const store = new Vuex.Store(vax);
Vue.use(Antd);
/* eslint-disable no-new */
window.$local = {
  store
}
new Vue({
  el: '#app',
  store,
  components: { App },
  template: '<App/>'
})



