<template>
  <div class="agora-view">
    <div
      :key="index"
      v-for="(stream, index) in AllStreams"
      :class="{
        'agora-video': true,
        'agora-fullscreen': stream && stream.fullscreen,
      }"
    >
      <div class="agora-toolbar">
        <a-icon type="sound" @click="mutedClick(stream)" /><a-icon
          type="fullscreen"
          @click="fullscreenClick(stream)"
        />
      </div>
      <StreamPlayer :stream="stream" :domId="stream.getId()"></StreamPlayer>
      <a-button
        v-if="LocalStream.getId() == stream.getId()"
        type="primary"
        @click="logout"
      >
        退出
      </a-button>
    </div>
  </div>
</template>

<script>
import StreamPlayer from "./stream-player";
import { mapGetters } from "vuex";
export default {
  props: ["rtc"],
  components: {
    StreamPlayer,
  },
  computed: {
    ...mapGetters(["LocalStream", "RemoteStreams"]),
    AllStreams() {
      if (this.LocalStream) {
        return [this.LocalStream, ...this.RemoteStreams];
      } else {
        return this.RemoteStreams;
      }
    },
  },
  methods: {
    fullscreenClick(stream) {
      stream.fullscreen = !stream.fullscreen;
    },
    mutedClick(stream) {
      stream.muted = !stream.muted;
      if (stream.muted) {
        stream.muteVideo();
      } else {
        stream.unmuteVideo();
      }
    },
    logout() {
      this.rtc.leaveEvent();
    },
  },
};
</script>
<style scoped>
.agora-view {
  display: flex;
  flex-wrap: wrap;
}
.agora-video {
  width: 320px;
  height: 240px;
  margin: 20px;
}
.agora-toolbar {
  padding: 5px;
  display: flex;
  justify-content: space-between;
}
.agora-fullscreen {
  position: absolute;
  width: 100%;
  height: 100%;
  margin: 0;
}
</style>



