<template>
  <div class="dashtable">
    <a-form :form="form">
      <a-form-item
        :label-col="formItemLayout.labelCol"
        :wrapper-col="formItemLayout.wrapperCol"
        label="App ID"
      >
        <a-input placeholder="Please input your App ID" v-model="appid" />
      </a-form-item>
      <a-form-item
        :label-col="formItemLayout.labelCol"
        :wrapper-col="formItemLayout.wrapperCol"
        label="Channel"
      >
        <a-input placeholder="Please input your Channel" v-model="channel" />
      </a-form-item>
      <a-form-item
        :label-col="formTailLayout.labelCol"
        :wrapper-col="formTailLayout.wrapperCol"
      >
        <a-button type="primary" @click="check" :loading="loading">
          加入会议
        </a-button>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>
import RTCWorkflow from "../utils/agora-rtc-workflow";
const formItemLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 8 },
};
const formTailLayout = {
  labelCol: { span: 4 },
  wrapperCol: { span: 8, offset: 4 },
};
export default {
  data() {
    return {
      formItemLayout,
      formTailLayout,
      form: this.$form.createForm(this, { name: "dynamic_rule" }),
      appid: "78a7209b80214ebc8292ba4ccc6717cb",
      channel: "rrrtest",
      loading: false,
    };
  },
  props: ["rtc"],
  methods: {
    AppidChange(value) {
      this.appid = value.data;
    },
    ChannelChange(value) {
      this.channel = value.data;
    },
    check() {
      if (!this.appid || !this.channel) {
        this.$message.error("appid 和 channel 不能为空");
        return;
      }
      this.form.validateFields(async (err) => {
        if (!err) {
          try {
            this.loading = true;
            let res = await this.rtc.Init();
            await this.rtc.joinEvent({
              appid: this.appid,
              channel: this.channel,
            });
            $local.store.dispatch("IsJoin", true);
          } catch (err) {
            debugger;
            this.$message.error(err);
            console.error(err);
            $local.store.dispatch("IsJoin", false);
          } finally {
            this.loading = false;
          }
        }
      });
    },
  },
};
</script>

<style scoped>
.dashtable {
  padding-top: 100px;
}
</style>


