
import RTCClient from "./agora-rtc-client";

export default class RTCWorkflow {
  constructor() {
    this.rtc = null;
  }

  Init() {
    this.rtc = new RTCClient()
    let rtc = this.rtc
    rtc.on('stream-added', (evt) => {
      let { stream } = evt
      rtc.client.subscribe(stream)
    })

    rtc.on('stream-subscribed', (evt) => {
      let { stream } = evt
      let remoteStreams = $local.store.state.RemoteStreams;
      if (!remoteStreams.find((it) => it.getId() === stream.getId())) {
        stream.muted = false;
        stream.fullscreen = false;
        remoteStreams.push(stream)
      }
    })

    rtc.on('stream-removed', (evt) => {
      let { stream } = evt
      let remoteStreams = $local.store.state.RemoteStreams;
      $local.store.dispatch("RemoteStreams", remoteStreams.filter((it) => it.getId() !== stream.getId()))
    })

    rtc.on('peer-online', (evt) => {
      this.$message(`Peer ${evt.uid} is online`)
    })

    rtc.on('peer-leave', (evt) => {
      $local.store.dispatch("RemoteStreams", remoteStreams.filter((it) => it.getId() !== evt.uid))
    })
  }

  async joinEvent(option) {
    let { appid, channel } = option;
    let res = await this.rtc.joinChannel(option);
    let localStream = await this.rtc.publishStream();
    localStream.muted = false;
    localStream.fullscreen = false;
    $local.store.dispatch("LocalStream", localStream)
  }

  async leaveEvent() {
    await this.rtc.leaveChannel()
    $local.store.dispatch("IsJoin", null)
    $local.store.dispatch("LocalStream", null)
    $local.store.dispatch("RemoteStreams", [])
  }
}


