<template>
  <div>
    <login v-if="!IsJoin" :rtc="rtc"/>
    <Room v-else  :rtc="rtc"/>
  </div>
</template>

<script>
import RTCWorkflow from "../utils/agora-rtc-workflow";
import { mapGetters } from "vuex";
import Login from "./Login.vue";
import Room from "./Room.vue";
export default {
  data(){
    return {
      rtc:null
    }
  },
  components: { Login, Room },
  computed: mapGetters(["IsJoin"]),
  created() {
    this.rtc = new RTCWorkflow();
  },
};
</script>


